.strike {
    text-decoration: line-through;
    border: none;
}

.list_item_container {
    display: flex;
    align-items: center;
    padding: 2px;

    .delete_item_button {
        display: flex;
        align-items: center;

        height: 1rem;
        border: none;
        border-radius: 10px;
        padding: 3px;
    }

    img {
        width: 1rem;
        height: 1rem;
    }
}

.list_item {
    display: flex;
    max-width: 5rem;

    -moz-user-select:none; /* firefox */
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE*/
    user-select: none; /* Standard syntax */

    cursor: pointer;

    padding: 1px;

    border-bottom: solid black 1px;
}