.strike {
    text-decoration: line-through;
}

.item_list_container {

    ul {
        background-color: #FFFFFF;
    }

    .item_list {
        margin-left: 2rem;
        max-width: 30%;
        height: 20rem;
        padding: 5px;

        // position: absolute;
        top: 0; left: 0; bottom: 0; right: 0;
        box-shadow: inset 1px 2px 4px 3px rgba(0,0,0,0.3);
        overflow: auto;

        list-style-type: none;
    }
}    